"use client"; // Bu komponentni client sifatida belgilash

import useGlobalStore from "@/store/my-store";
import useAuthStore from "@/store/MyAuthState";
import { getRandomID } from "@/utils/number";
import { PlusOutlined } from "@ant-design/icons";
import { Button, Drawer, Form, Input } from "antd";
import FormItem from "antd/es/form/FormItem/index.js";
import axios from "axios";

function AddStudent({ onClose, open, showDrawer }: any) {
  const MyAuthState = useAuthStore();

  return (
    <>
      <Button type="primary" onClick={showDrawer} icon={<PlusOutlined />}>
        Student qo'shish
      </Button>
      <Drawer
        title="Yangi student qo'shish"
        width={500}
        onClose={onClose}
        open={open}
        destroyOnClose
      >
        <Form
          onFinish={async (values) => {
            try {
              const res = await axios.post(
                `https://nt.softly.uz/api/app/users`, // URL
                {
                  ...values,
                  id: getRandomID(), // Yangi ID qo'shish
                },
                {
                  headers: {
                    Authorization: `Bearer ${MyAuthState.token}`, // To'g'ri Authorization
                  },
                }
              );

              // Muvaffaqiyatli javobni ko'rsatish yoki saqlash
              console.log("Student qo'shildi:", res.data);
              onClose(false); // Formani yopish
            } catch (error) {
              console.log("Xatolik:", error); // Xatolikni ko'rsatish
            }
          }}
        >
          <FormItem
            label="Ism"
            name="name"
            rules={[{ required: true, message: "Ism kiritilmadi!!!" }]}
          >
            <Input />
          </FormItem>
          <FormItem
            label="Email"
            name="email"
            rules={[{ required: true, message: "Email kiritilmadi!!!" }]}
          >
            <Input />
          </FormItem>
          <FormItem
            label="Roli"
            name="role"
            rules={[{ required: true, message: "Roli kiritilmadi!!!" }]}
          >
            <Input />
          </FormItem>
          <FormItem>
            <Button type="primary" htmlType="submit">
              Submit
            </Button>
          </FormItem>
        </Form>
      </Drawer>
    </>
  );
}

export default AddStudent;
